// Modal code from https://sabe.io/tutorials/how-to-create-modal-popup-box
// I added new parts and changed the original code accordingly. 

.modal {
    position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        visibility: hidden;
        transform: scale(1.1)
}
.modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 1rem 1.5rem;
    width: 24rem;
    border-radius: 0.5rem;
    word-wrap: break-word;
}

.close-button {
        float: right;
        width: 1.5rem;
        line-height: 1.5rem;
        text-align: center;
        cursor: pointer;
        border-radius: 0.25rem;
        background-color: $red;
        margin-left: 20px;
    }
.close-button:hover {
        background-color: darkgray;
}

.show-modal {
        opacity: 1;
        visibility: visible;
        transform: scale(1.0);
        transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
}

h2 {
    font-size: 2rem;
}


.deleteButton {
    border-radius: 5px;
}